<template>

    <div class="col-md-6">
        <h4>Tutorials List</h4>
        <ul class="list-group">
            <li class="list-group-item" :class="{ active: index == currentIndex }"
                v-for="(tutorial, index) in tutorials" :key="index" @click="setActiveTutorial(tutorial, index)">
                {{ tutorial.title }}
            </li>
        </ul>
        </div>
</template>

<script>
import services from "../services/services";
export default {
  name: "tutorials-list",
  data() {
    return {
      tutorials: [],
      currentTutorial: null,
      currentIndex: -1,
      title: ""
    };
  },
  methods: {
    retrieveTutorials() {
        services.getAll()
        .then(response => {
          this.tutorials = response.data;
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },
    refreshList() {
      this.retrieveTutorials();
      this.currentTutorial = null;
      this.currentIndex = -1;
    },
    mounted() {
    this.retrieveTutorials();
  }
}}
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
</style>